:host {
  width: 100%;
  min-width: 320px;
  display: flex;
  flex-direction: column;
  height: 100%;
  // min-height: 1000px;
  position: relative;
}

.background {
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  // background-image: url('/prueba separados bg PMeL.svg');
  background-image: var(--backgroundImage);
  background-size: cover;
  background-position: left;
  background-repeat: no-repeat;
  // min-height: calc(100vh + 100px);
  width: 100%;
  height: calc(100% + 100px);
  transform: translateY(-100px);
  z-index: 0;

  display: flex;
  align-items: center;
  overflow: hidden;
  // justify-content: flex-end;
}

.foreground-img {
  transform: translateX(80px);
  transform: translateY(128px);
}

.main-container {
  height: 100%;
  width: 100%;
  min-height: calc(100vh - 100px);
  display: flex;
  // padding-top: 3rem;
  // background-color: #03142e;
  // background-image: var(--backgroundImage);
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.382);
  position: absolute;
  z-index: 0;
  display: none;
}

.main-inner-container {
  width: 100%;
  padding-right: 128px;
  padding-left: 128px;
  padding-top: 32px;
  padding-bottom: 32px;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  gap: 2rem;
}

@media (max-width: 1310px) {
  .foreground-container {
    max-width: 540px;
    width: 100%;
    img {
      width: 100%;
    }
    .foreground-img {
      transform: translateY(202px);
    }
  }
}

@media (max-width: 1256px) {
  .main-container:not(.institution-bg) {
    background: none;
  }
}

@media (max-width: 1200px) {
  .main-inner-container {
    padding-left: 64px;
    padding-right: 64px;
  }

  .main-container {
    padding-top: 0;
  }

  .foreground-container {
    display: none;
  }
}
@media (max-width: 1000px) {
  .backdrop {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
  }
  .foreground-img {
    display: none;
  }
}

@media (max-width: 820px) {
  .main-inner-container {
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media (max-width: 600px) {
  .main-inner-container {
    width: 100%;
    padding-left: 12px;
    padding-right: 12px;
  }
}
